body {
  color: #111;
}

body * {
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
}

h1,
h2,
h3,
h4,
h5,
header,
div,
img {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
}

:focus,
a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
blockquote {
  font-weight: 400;
  color: inherit;
}

ol,
ul {
  list-style: none;
}

body.loading {
  overflow: hidden;
}
